.u-inner {
    max-width: 1200px;
    margin: 0 auto;
}
.u-inner1 {
    max-width: 1400px;
    margin: 0 auto;
}
.u-bg-blue {
    background-color: #1A49A5;
}
.u-bg-thin-blue {
    background-color: #24ABF1;
}
.u-bg-green {
    background-color: #F5BE3F;
}
.u-bg-gray {
    background-color: #3BB1A1;
}
.u-bg-thin-red {
    background-color: #F85C66;
}
.u-bg-brown {
    background-color: #825B3A;
}
// パソコン非表示用
.u-pc-block {
    display: block;
}
// スマホ非表示用
.u-sp-block {
    display: none;
}
@media(max-width:768px) {
    .u-pc-block {
        display: none;
    }
    .u-sp-block {
        display: block;
    }
}