/* header */

.header {
    background-color: white;
    position: sticky;
    top: 0;
    padding: 20px 0;
    z-index: 100;
    &-item {
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    .logo {
        figure {
            img {
                width: 140px;
            }
        }
    }
    .link {
        margin-left: 60px;
        .lead {
            p {
                font-size: 13px;
                &::before {
                    content: "";
                    background: url(../img/ico-ha-circle.png) no-repeat;
                    background-size: cover;
                    width: 21.57px;
                    height: 14.83px;
                    display: inline-block;
                    margin-right: 6.4px;
                    vertical-align: middle;
                }
            }
        }
        .group {
            display: flex;
            flex-direction: row;
            align-items: center;
            &-link {
                a {
                    cursor: pointer;
                    font-size: 16px;
                    font-weight: bold;
                    line-height: (38 / 16);
                    margin-right: 17px;
                }
                a:nth-child(1) {
                    &::after {
                        content: "";
                        background: url(../img/ico-arrow-down.svg) no-repeat;
                        background-size: cover;
                        width: 9px;
                        height: 9px;
                        display: inline-block;
                        margin-left: 7px;
                        vertical-align: middle;
                    }
                }
                a:nth-child(2) {
                    &::after {
                        content: "";
                        background: url(../img/ico-arrow-down.svg) no-repeat;
                        background-size: cover;
                        width: 9px;
                        height: 9px;
                        display: inline-block;
                        margin-left: 7px;
                    }
                }
            }
            &-btn {
                a {
                    height: 40px;
                    font-weight: bold;
                    color: white;
                    font-size: 14px;
                    border-radius: 5px;
                    letter-spacing: 0px;
                    display: inline-block;
                    padding: 5px 8px;
                }
                a:hover {
                    opacity: 0.9;
                }
                a:nth-child(1) {
                    width: 160px;
                    background-color: #06C755;
                    border: 1px solid #06C755;
                    margin-left: 12px;
                    &::before {
                        content: "";
                        background: url(../img/ico-line.svg) no-repeat;
                        background-size: cover;
                        width: 29px;
                        height: 28px;
                        margin-right: 7px;
                        display: inline-block;
                        vertical-align: middle;
                    }
                }
                a:nth-child(2) {
                    width: 170px;
                    background-color: #E85400;
                    border: 1px solid #E85400;
                    margin-left: 10px;
                    &::before {
                        content: "";
                        background: url(../img/ico-calender-white.png) no-repeat;
                        background-size: cover;
                        width: 27px;
                        height: 28px;
                        margin-right: 7px;
                        display: inline-block;
                        vertical-align: middle;
                    }
                }
            }
        }
    }
    .sp-lead {
        margin-bottom: 5px;
        p {
            font-size: 10px;
            text-align: center;
            &::before {
                content: "";
                background: url(../img/ico-ha-circle.png) no-repeat;
                background-size: cover;
                width: 10px;
                height: 7px;
                display: inline-block;
                margin-right: 6.4px;
                vertical-align: middle;
            }
        }
    }
    .sp-link {
        .line {
            width: 28px;
            height: 16px;
            cursor: pointer;
            span {
                width: 25px;
                height: 2px;
                background: #000;
                display: block;
                transition: 0.4s;
                &:not(:last-child) {
                    margin-bottom: 5px;
                }
            }
        }
        p {
            font-family: "Noto Sans JP";
            font-weight: bold;
            font-size: 7px;
            line-height: 17px;
            color: #000;
        }
        &.change {
            .line {
                span {
                    &:first-child {
                        transform: rotate( -45deg) translate(0px, 3px);
                    }
                    &:nth-child(2) {
                        display: none;
                    }
                    &:last-child {
                        transform: rotate( 45deg) translate(-2px, -5px);
                    }
                }
            }
        }
    }
}


/* banner */

.banner {
    position: relative;
    background: url(../img/bnr-img.png) no-repeat;
    background-size: cover;
    background-position: center;
    height: 85vh;
    display: flex;
    align-items: flex-end;
    figure {
        display: flex;
        img {
            width: 100%;
        }
    }
    .bnrtxt {
        width: 1200px;
        margin-bottom: 70px;
        &-lead {
            display: flex;
            flex-direction: row;
            align-items: center;
            &__ico {
                figure {
                    img {
                        width: 62.74px;
                        height: 67.56px;
                    }
                }
                margin-right: 8.3px;
            }
        }
        &-item {
            color: white;
            h2 {
                font-size: 20px;
                font-weight: bold;
                span {
                    position: relative;
                    &::after {
                        content: "";
                        width: 35.95px;
                        height: 22.28px;
                        background: url(../img/ico-three-line.png) no-repeat;
                        background-size: cover;
                        position: absolute;
                        right: -70px;
                        top: 9px;
                    }
                }
            }
            h1 {
                font-size: 36px;
                font-weight: bold;
                position: relative;
                span:nth-child(2) {
                    position: absolute;
                    top: -2px;
                    left: 222px;
                }
                span:nth-child(3) {
                    margin-left: 264px;
                }
            }
        }
        &-txt {
            margin-top: 10px;
            p {
                color: white;
                font-size: 18px;
                font-weight: bold;
                line-height: (30 / 18);
            }
        }
    }
}


/* flo-btn */

.flo-btn {
    position: fixed;
    bottom: 40px;
    right: 80px;
    &__item {
        position: relative;
        &-close {
            position: absolute;
            top: -15px;
            right: -15px;
            cursor: pointer;
        }
    }
}


/* service */

.service {
    margin-top: 90px;
    &-item {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}


/* reservation */

.reservation {
    background-color: #D8D1BD;
    padding: 54px 0 49px 0;
    margin-top: 51px;
    &-item {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        position: relative;
    }
    &__lead {
        position: relative;
        &-abs {
            position: absolute;
            right: 58px;
            top: -13px;
        }
    }
}


/* intro */

.intro {
    margin-top: 90px;
    position: relative;
    &__lead {
        position: relative;
        display: flex;
        flex-direction: row;
        figure {
            display: flex;
            max-width: 800px;
            img {
                width: 100%;
            }
            @media(max-width:1400px) {
                width: 58%;
            }
        }
        h2 {
            width: 455px;
            font-size: 32px;
            line-height: (59 / 32);
            margin-left: 45px;
            margin-top: 80px;
        }
        z-index: 10;
    }
    &-item {
        z-index: 0;
        position: relative;
        background-color: #E85400;
        padding-top: 96px;
        padding-bottom: 25px;
        margin-top: -395px;
        @media(max-width: 1350px) {
            margin-top: -300px;
        }
        &__txt {
            display: flex;
            justify-content: flex-end;
            &-group {
                width: 450px;
                color: white;
                p:nth-child(1) {
                    font-size: 25px;
                    font-weight: bold;
                    line-height: (36 / 25);
                }
                p:nth-child(2) {
                    font-size: 16px;
                    line-height: (32 / 16);
                    margin-top: 15px;
                }
            }
        }
        &-flex {
            margin-top: 126px;
            position: relative;
        }
        .ttl {
            display: flex;
            flex-direction: row;
            &__txt {
                margin-left: 102px;
                color: white;
                width: 416px;
                p {
                    font-size: 24px;
                    font-weight: bold;
                    line-height: (35 / 24);
                }
            }
            figure {
                margin-left: 25px;
            }
        }
        .txt {
            position: absolute;
            top: 100px;
            left: 102px;
            width: 416px;
            p {
                color: white;
                line-height: (32 / 16);
            }
        }
        &::after {
            content: "";
            background-color: #E85400;
            height: 100%;
            position: absolute;
            width: 50%;
            top: 0;
            right: -12%;
            z-index: -1;
            @media(max-width: 1280px) {
                right: -3.3%;
            }
        }
    }
    .abs_img1 {
        position: absolute;
        right: 0;
        top: -70px;
    }
}


/* store */

.store {
    margin-top: 90px;
    background-color: #F5F5F5;
    padding: 40px 0;
    position: relative;
    &-item {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        &__col {
            width: 380px;
            text-align: center;
        }
        h2 {
            font-size: 19px;
            margin-top: 12px;
            font-weight: bold;
            letter-spacing: 0px;
        }
        p:nth-child(3) {
            font-size: 32px;
            font-weight: 500;
            margin-top: 20.7px;
        }
        p:nth-child(4) {
            color: #E85400;
            font-size: 20px;
        }
        p:nth-child(5) {
            margin-top: 26px;
            line-height: (32 / 16);
            text-align: left;
            font-family: Yu Gothic;
        }
    }
}


/* campaign */

.campaign {
    .c-ttl1 {
        margin-top: 90px;
    }
    position: relative;
    &-item {
        margin-top: 34px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &-btn {
        text-align: center;
        margin-top: 52px;
        margin-bottom: 90px;
        a {
            font-size: 16px;
            font-weight: bold;
            color: #E85400;
            width: 200px;
            height: 50px;
            background-color: white;
            border: 1px solid #E85400;
            border-radius: 5px;
            display: inline-block;
            padding: 10px;
        }
    }
}


/* usefulinfo */

.usefulinfo {
    background-color: #F5F5F5;
    padding: 68px 0 32px 0;
    &-item {
        margin-top: 35px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &-btn {
        text-align: center;
        margin-top: 35px;
        a {
            font-size: 16px;
            font-weight: bold;
            color: #E85400;
            width: 200px;
            height: 50px;
            background-color: white;
            border: 1px solid #E85400;
            border-radius: 5px;
            display: inline-block;
            padding: 10px;
        }
    }
}


/* news */

.news {
    max-width: 1020px;
    margin: 90px auto 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    &-lead {
        width: 200px;
        text-align: center;
        h2 {
            font-size: 32px;
            font-weight: 500;
        }
        p {
            font-size: 20px;
            color: #E85400;
        }
        a {
            width: 100%;
            height: 50px;
            margin-top: 30px;
            background-color: white;
            border: 1px solid #E85400;
            color: #E85400;
            font-size: 16px;
            font-weight: bold;
            border-radius: 5px;
            display: inline-block;
            padding: 10px;
        }
    }
    &-list {
        margin-left: 60px;
        width: 100%;
        &-row {
            display: flex;
            flex-direction: row;
            align-items: center;
            border-bottom: 1px dotted #848484;
            padding-bottom: 27px;
            padding-top: 27px;
            p:nth-child(1) {
                display: flex;
                flex-direction: row;
                align-items: center;
                span:nth-child(1) {
                    padding-left: 10px;
                    font-size: 14px;
                }
                span:nth-child(2) {
                    margin-left: 40px;
                    color: white;
                    padding: 5px 26px;
                    font-size: 12px;
                    font-weight: bold;
                    line-height: 1;
                }
            }
            p:nth-child(2) {
                margin-left: 40px;
                font-size: 16px;
                line-height: (24 / 16);
            }
            &:first-child {
                border-top: 1px dotted #848484;
            }
        }
    }
}


/* sooncheck */

.sooncheck {
    &-item {
        margin-top: 90px;
        background-color: #E85400;
        padding: 39px 0 47px 0;
        border-radius: 10px;
        position: relative;
        &-txt {
            display: flex;
            flex-direction: column;
            align-items: center;
            p:nth-child(1) {
                color: white;
                font-weight: bold;
                font-size: 26px;
            }
            h2 {
                font-size: 61px;
                font-weight: 500;
                color: white;
                span {
                    position: relative;
                    &::before {
                        content: "";
