                        background-size: cover;
                        width: 59.25px;
                        height: 71.86px;
                        display: inline-block;
                        margin-right: 13.6px;
                        vertical-align: sub;
                    }
                }
            }
            a {
                margin-top: 15px;
                background-color: #EC7633;
                border: 1px dotted white;
                color: white;
                font-size: 18px;
                font-weight: 500;
