                height: 48px;
                border-radius: 50px;
                display: inline-block;
                padding: 10px;
                text-align: center;
                &:hover {
                    opacity: 0.9;
                }
                span {
                    position: relative;
                    &::after {
                        content: "";
                        background: url(../img/ico-arrow-right.svg) no-repeat;
                        background-size: cover;
                        position: absolute;
                        top: calc(50% - 5.68px);
                        right: -15px;
                        width: 11.36px;
                        height: 11.36px;
                        margin-left: 15px;
                    }
                }
