        }
        &-img {
            &__abs1 {
                position: absolute;
                left: 25px;
                bottom: 0;
                text-align: center;
                p {
                    margin-left: -40px;
                    font-size: 17px;
                    color: white;
                    font-weight: 500;
                    margin-bottom: 5px;
                }
                figure {
                    display: flex;
                }
            }
            &__abs2 {
                position: absolute;
                right: 25px;
                bottom: 0;
                p {
                    font-size: 17px;
                    color: white;
                    font-weight: 500;
                    transform: rotate(-10deg);
                    text-align: right;
                }
                figure {
                    display: flex;
                }
            }
        }
    }
}


/* line-item */

.line-item {
    margin-top: 40px;
    background-color: #58514B;
    padding: 30px 30px 30px 46px;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    &__txt {
        width: 607px;
        h2 {
            font-size: 50px;
            font-weight: bold;
            color: #06C755;
        }
        p:nth-child(2) {
            font-size: 22px;
            margin-top: 9px;
            font-weight: 500;
            line-height: (32 / 22);
            color: white;
        }
        p:nth-child(3) {
            color: white;
            line-height: (28 / 16);
            margin-top: 10px;
        }
    }
    &__link {
        width: 480px;
        margin-left: 30px;
        figure {
            text-align: right;
            display: flex;
            justify-content: flex-end;
        }
        a {
            width: 100%;
            height: 50px;
            background-color: #06C755;
            border: 1px solid #06C755;
            border-radius: 5px;
            color: white;
            font-size: 18px;
            font-weight: 500;
            letter-spacing: 0px;
            display: inline-block;
            padding: 7px 10px;
            text-align: center;
            span {
                position: relative;
                &::before {
                    content: "";
                    background: url(../img/ico-line.svg) no-repeat;
                    background-size: cover;
                    width: 29px;
                    height: 28px;
                    display: inline-block;
                    margin-right: 9px;
                    vertical-align: middle;
                }
                &:hover {
                    opacity: 0.9;
                }
            }
        }
    }
}


/* entry */

.entry {
    margin-top: 90px;
    background-color: #D8D1BD;
    padding: 34px 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    .link1 {
        font-size: 36px;
        font-weight: 500;
        display: inline-block;
        padding: 8px;
        span {
            position: relative;
            &::before {
                content: "";
                background: url(../img/ico-phone.png) no-repeat;
                background-size: cover;
                width: 25.26px;
                height: 32.95px;
                position: absolute;
                left: -30px;
                top: calc(50% - 14.475px);
            }
        }
    }
    p {
        line-height: (38 / 16);
        margin-left: 9px;
        font-weight: 500;
    }
    .link2 {
        width: 230px;
        height: 50px;
        border-radius: 5px;
        background-color: white;
        border: 1px solid #E85400;
        margin-left: 29px;
        font-size: 16px;
        font-weight: bold;
        color: #E85400;
        letter-spacing: 0px;
        display: inline-block;
        padding: 8px;
        text-align: center;
        &::before {
            content: "";
            background: url(../img/ico-mail.png) no-repeat;
            background-size: cover;
            width: 28.86px;
            height: 22.65px;
            display: inline-block;
            vertical-align: middle;
            margin-right: 9px;
        }
    }
}


/* page-top */

.page-top {
    margin-top: 90px;
    text-align: right;
    font-size: 15px;
    span {
        color: #E85400 !important;
    }
    span:nth-child(1) {
        margin-right: 10px;
    }
}


/* footer */

.footer {
    background-color: #F5F5F5;
    padding: 41px 0 39px 0;
    margin-top: 40px;
}


/* footer-item */

.footer-item {
    display: flex;
    flex-direction: row;
    position: relative;
    &__col {
        margin-left: 85px;
        &:first-child {
            margin-left: unset;
        }
        figure {
            display: flex;
            flex-direction: column;
            img:nth-child(1) {
                width: 100px;
            }
            img:nth-child(2) {
                width: 203px;
                margin-top: 30px;
            }
        }
        &-adress {
            margin-top: 10px;
            p {
                font-size: 14px;
                line-height: (22 / 14);
                margin-top: 10px;
            }
            figure {
                display: flex;
                margin-top: 15px;
                img {
                    width: 250px !important;
                }
            }
        }
        &>p {
            font-size: 12px;
            line-height: (18 / 12);
            margin-top: 26px;
        }
        &-ttl {
            p {
                font-size: 16px;
                font-weight: 500;
                line-height: 1;
            }
        }
        &-link {
            margin-top: 26px;
            display: flex;
            flex-direction: column;
            a {
                font-size: 14px;
                margin-top: 15px;
                &:first-child {
                    margin-top: unset;
                }
            }
        }
        &-otherlink {
            display: flex;
            flex-direction: column;
            a {
                font-size: 16px;
                margin-top: 18px;
                font-weight: 500;
                &:first-child {
                    margin-top: unset;
                }
                &:nth-child(3) {
                    position: relative;
                    &::after {
                        content: "";
                        background: url(../img/ico-attach.svg) no-repeat;
                        background-size: cover;
                        width: 12.04px;
                        height: 12.2px;
                        display: inline-block;
                        margin-left: 9px;
                    }
                }
                &:nth-child(4) {
                    position: relative;
                    &::after {
                        content: "";
                        background: url(../img/ico-attach.svg) no-repeat;
                        background-size: cover;
                        width: 12.04px;
                        height: 12.2px;
                        display: inline-block;
                        margin-left: 9px;
                    }
                }
            }
            figure {
                margin-top: 15px;
                width: 40px;
            }
        }
    }
    &__abs {
        position: absolute;
        right: 0;
        bottom: -39px;
        figure {
            display: flex;
        }
    }
}

