.c-overlay {
    overflow-y: auto;
    overflow-x: hidden;
    position: fixed;
    height: 100%;
    z-index: 99;
    -webkit-font-smoothing: antialiased;
    /* to stop flickering of text in safari */
    right: 0;
    transition: width 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0);
    width: 0;
    &-content {
        overflow-y: auto;
        position: relative;
        padding: 90px 20px 10px;
        width: 100%;
        background: #E85400;
        height: auto;
        .panel {
            background-color: white;
            padding: 0 10px 20px;
            border-radius: 10px;
            &_li {
                border-bottom: 1px dotted black;
            }
            .link {
                padding: 15px 24px;
                display: block;
                font-family: "Yu Gothic";
                font-weight: bold;
                font-size: 16px;
                line-height: 24px;
                color: #000;
                position: relative;
                &::after {
                    @include icon_img(6.5px, 10.5px, '../img/sp/arrow_black.svg');
                    position: absolute;
                    top: 22px;
                    right: 27px;
                }
                &.collapsible {
                    &::after {
                        @include icon_img(10.5px, 10.5px, '../img/sp/plus.svg');
                        top: 22px;
                    }
                    &.active {
                        &::after {
                            @include icon_img(10.5px, 1.5px, '../img/sp/minus.svg');
                            top: 27px;
                        }
                    }
                }
                img {
                    width: 25px;
                    margin-right: 15px;
                }
            }
            .content {
                max-height: 0;
                overflow: hidden;
                transition: max-height 0.2s ease-out;
            }
            .car {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                padding-bottom: 20px;
                a {
                    display: block;
                    width: calc(50% - 5px);
                    margin-bottom: 10px;
                    color: white;
                    font-family: "Noto Sans JP";
                    font-weight: bold;
                    font-size: 15px;
                    text-align: center;
                    padding: 4px;
                    &:nth-child(1) {
                        background-color: #1A49A5;
                        img {
                            width: 18px;
                        }
                    }
                    &:nth-child(2) {
                        background-color: #F5BE3F;
                        img {
                            width: 13px;
                        }
                    }
                    &:nth-child(3) {
                        background-color: #3BB1A1;
                        img {
                            width: 14.5px;
                        }
                    }
                    &:nth-child(4) {
                        background-color: #F85C66;
                        img {
                            width: 15.5px;
                        }
                    }
                    &:nth-child(5) {
                        background-color: #825B3A;
                        img {
                            width: 31.5px;
                        }
                    }
                }
            }
            .box {
                padding: 0 30px 20px 24px;
                a {
                    padding: 4.5px 0;
                    font-family: "Noto Sans JP";
                    font-weight: bold;
                    font-size: 14px;
                    line-height: 20px;
                    position: relative;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    &:after {
                        @include icon_img(6.5px, 10.5px, '../img/sp/arrow_yellow.svg');
                    }
                }
            }
            .win {
                display: flex;
                padding: 24px 0;
                a {
                    display: block;
                    text-align: center;
                    width: 50%;
                    font-family: "Yu Gothic";
                    font-weight: bold;
                    font-size: 12px;
                    line-height: 1.2;
                    color: #000;
                    position: relative;
                    span {
                        &::after {
                            @include icon_img(10px, 10px, '../img/sp/win.svg');
                            margin-left: 3px;
                        }
                    }
                    &:first-child {
                        &::after {
                            content: "";
                            width: 1px;
                            height: 16px;
                            background-color: black;
                            display: block;
                            position: absolute;
                            right: 0;
                            top: -2px;
                        }
                    }
                }
            }
        }
        .bottom {
            padding-bottom: 30px;
            p {
                padding: 22.5px 0 0 22.5px;
                font-family: HuiFontP;
                font-weight: normal;
                font-size: 14px;
                line-height: 1.5;
                color: #fff;
            }
            .line {
                width: 250px;
                margin-top: -20px;
            }
            .man {
                width: 105px;
                position: absolute;
                bottom: 0;
                right: 20px;
            }
        }
    }
    &.change {
        width: 100%;
    }
}