.c-ttl {
    text-align: center;
    p:nth-child(2) {
        margin-top: 20px;
        font-size: 20px;
        font-weight: bold;
    }
    h2 {
        font-size: 32px;
        margin-top: 10px;
        font-weight: 600;
    }
    p:nth-child(4) {
        font-size: 20px;
        color: #E85400;
    }
    p:nth-child(5) {
        margin-top: 30px;
        line-height: (32 / 16);
    }
}
.c-ttl1 {
    text-align: center;
    h2 {
        font-size: 32px;
        font-weight: 500;
        margin-top: 14.5px;
    }
    p {
        font-size: 20px;
        color: #FF4D04;
    }
}
.c-item1 {
    width: 380px;
    margin: 11px 0 39px 0;
    figure {
        display: flex;
    }
    &__lead {
        p {
            text-align: center;
            padding: 7px 0;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            position: relative;
            span:nth-child(1) {
                margin-right: 11px;
            }
            span:nth-child(2) {
                color: white;
                line-height: (29 / 20);
                font-size: 20px;
                font-weight: bold;
            }
            &::after {
                content: "";
                background: url(../img/ico-arrow-right.svg) no-repeat;
                background-size: cover;
                width: 7px;
                height: 7px;
                position: absolute;
                right: 20px;
                top: calc(50% - 3.5px);
            }
        }
    }
    &__txt {
        margin-top: 13px;
        p {
            font-size: 16px;
            font-weight: bold;
            line-height: (26 / 16);
            font-family: Yu Gothic, sans-serif;
        }
    }
}
.c-item2 {
    width: 580px;
    padding: 25px 0;
    background-color: white;
    text-align: center;
    margin-top: 32px;
    border-radius: 5px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.8) 0px 3px 7px -3px;
    p:nth-child(2) {
        font-size: 20px;
        font-weight: bold;
        color: #E85400;
        line-height: (29 / 20);
    }
    p:nth-child(3) {
        font-size: 16px;
        line-height: (26 / 16);
        margin-top: 9px;
    }
    a {
        width: 208px;
        height: 50px;
        background-color: black;
        border: 1px solid black;
        margin-top: 13px;
        border-radius: 5px;
        display: inline-block;
        padding: 5px;
        span {
            position: relative;
            font-size: 16px;
            color: white;
            font-weight: bold;
            line-height: (24 / 16);
            letter-spacing: 0px;
            &::before {
                content: "";
                background: url(../img/ico-calender-white.png) no-repeat;
                background-size: cover;
                width: 33.04px;
                height: 34.26px;
                display: inline-block;
                margin-right: 8.5px;
                vertical-align: middle;
            }
        }
    }
    a:hover {
        opacity: 0.9;
    }
}
.c-item3 {
    width: 380px;
    figure {
        display: flex;
    }
    p {
        text-align: center;
    }
    p:nth-child(2) {
        color: #E85400;
        font-weight: bold;
        margin-top: 10px;
    }
    p:nth-child(3) {
        font-size: 20px !important;
        font-weight: bold;
        margin-top: unset !important;
        span {
            position: relative;
            &::after {
                content: "";
                border-bottom: 2px solid #E85400;
                width: 100px;
                position: absolute;
                bottom: -12px;
                left: calc(50% - 50px);
            }
        }
    }
    .link1 {
        font-weight: bold;
        color: black !important;
        margin-top: 20px;
        display: flex;
        justify-content: center;
        span {
            position: relative;
            &::before {
                content: "";
                background: url(../img/ico-google.svg) no-repeat;
                background-size: cover;
                width: 16.5px;
                height: 23.53px;
                display: inline-block;
                vertical-align: middle;
                margin-right: 6px;
            }
        }
    }
    .link2 {
        width: 100%;
        background-color: #E85400;
        border: 1px solid #E85400;
        height: 40px;
        border-radius: 50px;
        color: white;
        font-weight: bold;
        font-size: 14px;
        margin-top: 15px;
        position: relative;
        display: block;
        text-align: center;
        padding: 7px;
        cursor: pointer;
        span {
            &::after {
                content: "";
                background: url(../img/ico-arrow-right1.svg) no-repeat;
                background-size: cover;
                width: 7px;
                height: 11.36px;
                display: inline-block;
                margin-left: 10px;
            }
        }
        &:hover {
            opacity: 0.9;
        }
    }
}
.c-item4 {
    width: 380px;
    position: relative;
    figure {
        display: flex;
    }
    h2 {
        margin-top: 10px;
        font-size: 20px;
        line-height: (29 / 20);
        font-weight: bold;
        color: #E85400;
    }
    p {
        font-size: 12px;
        line-height: (22 / 12);
        font-family: Yu Gothic, sans-serif;
        margin-top: 15px;
    }
    p:nth-child(4) {
        position: absolute;
        top: 0;
        left: 0;
        padding: 5px 26px;
        color: white;
        font-weight: bold;
        font-size: 12px;
        font-family: Yu Gothic, sans-serif;
        line-height: 1;
        margin-top: unset;
    }
}
@media(max-width:768px) {
    .c-ttl {
        p:nth-child(2) {
            font-size: 26px;
        }
        h2 {
            font-size: 40px;
        }
        p:nth-child(4) {
            font-size: 24px;
        }
    }
    .c-ttl1 {
        h2 {
            font-size: 40px;
        }
        p {
            font-size: 24px;
        }
    }
    .c-item1 {
        margin: 20px 0 10px 0;
        width: 49%;
        &__txt {
            display: none;
        }
    }
    .c-item2 {
        width: 100%;
        margin-top: 20px;
    }
    .c-item3 {
        width: 100%;
        margin-top: 40px;
        margin-bottom: 50px;
        figure {
            width: 100%;
            img {
                width: 100%;
            }
        }
        p:nth-child(2) {
            font-size: 26px;
        }
        p:nth-child(3) {
            font-size: 36px !important;
            span {
                &::after {
                    width: 100%;
                    left: 0;
                }
            }
        }
        &-link {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-top: 20px;
            a {
                font-size: 30px;
                span {
                    &::before {
                        width: 25.5px;
                        height: 36.53px;
                        vertical-align: sub;
                        margin-right: 10px;
                    }
                }
            }
            a {
                width: 49%;
                height: 62px;
                font-size: 28px;
                span {
                    &::after {
                        width: 12px;
                        height: 18.36px;
                    }
                }
            }
        }
    }
    .c-item4 {
        width: 48.5%;
        margin-bottom: 40px;
        h2 {
            font-size: 30px;
            line-height: (40 / 30);
        }
        p {
            display: none;
        }
    }
}
@media(max-width:718px) {
    .c-ttl {
        p:nth-child(2) {
            font-size: 16px;
        }
        h2 {
            font-size: 32px;
        }
        p:nth-child(4) {
            font-size: 20px;
        }
        p:nth-child(5) {
            font-size: 16px !important;
            text-align: left;
        }
    }
    .c-ttl1 {
        h2 {
            font-size: 32px;
        }
        p {
            font-size: 20px;
        }
    }
    .c-item1 {
        &__lead {
            p {
                span:nth-child(2) {
                    font-size: 16px;
                }
            }   
        }
    }
    .c-item2 {
        p:nth-child(2) {
            font-size: 18px;
        }
    }
    .c-item3 {
        margin-bottom: unset;
        p:nth-child(2) {
            font-size: 16px;
        }
        p:nth-child(3) {
            font-size: 20px !important;
        }
        &-link {
            flex-direction: column;
            a {
                font-size: 16px;
                span {
                    &::before {
                        width: 16.5px;
                        height: 23.53px;
                    }
                }
            }
            a {
                width: 100%;
                font-size: 16px;
                span {
                    &::after {
                        width: 7px;
                        height: 11.36px;
                    }
                }
            }
        }
    }
    .c-item4 {
        h2 {
            font-size: 20px;
        }
    }
}